.section-kv {
    height: 1080px;
    position: relative;
    &__logo {
        width: 209px;
        height: 83px;
        background: transparent url('../images/ml-logo@2x.png') no-repeat 0 0;
        background-size: 209px 83px;
        position: absolute;
        left: 15px;
        top: 39px;
    }
    &__nav {
        display: flex;
        position: absolute;
        right: 12px;
        top: 40px;
        gap: 12px;
    }
    &__btn-sn,
    &__btn-fb,
    &__btn-home {
        background: transparent url('../images/kv-btn-bg@2x.png') no-repeat 0 0;
        display: block;
        font-size: 0;
        position: relative;
    }
    &__btn-sn {
        width: 246px;
        height: 84px;
        background-size: 414px auto;
        background-position: 0 0;
        &::after {
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            background: transparent url('../images/kv-btn-bg@2x.png') no-repeat 0 0;
            background-size: 414px auto;
            background-position: 0 -84px;
            transition: opacity 300ms ease;
            opacity: 0;
        }
        &:hover {
            &::after {
                opacity: 1;
            }
        }
    }
    &__btn-fb,
    &__btn-home {
        width: 84px;
        height: 84px;
        background-size: 414px auto;
    }
    &__btn-fb {
        background-position: -246px 0;
        &::after {
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            background: transparent url('../images/kv-btn-bg@2x.png') no-repeat 0 0;
            background-size: 414px auto;
            background-position: -246px -84px;
            transition: opacity 300ms ease;
            opacity: 0;
        }
        &:hover {
            &::after {
                opacity: 1;
            }
        }
    }
    &__btn-home {
        background-position: -330px 0;
        &::after {
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            background: transparent url('../images/kv-btn-bg@2x.png') no-repeat 0 0;
            background-size: 414px auto;
            background-position: -330px -84px;
            transition: opacity 300ms ease;
            opacity: 0;
        }
        &:hover {
            &::after {
                opacity: 1;
            }
        }
    }
    &__btn-download {
        position: absolute;
        width: 304px;
        height: 106px;
        background: transparent url('../images/btn-bg@2x.png') no-repeat 0 0;
        background-size: 100% auto;
        bottom: 36px;
        left: 50%;
        transform: translate3d(-50%,0,0);
        display: flex;
        justify-content: center;
        &::after {
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            background: transparent url('../images/btn-bg@2x.png') no-repeat 0 -106px;
            background-size: 100% auto;
            transition: opacity 300ms ease;
            opacity: 0;
            z-index: 0;
        }
        span {
            position: relative;
            z-index: 10;
            font-size: 40px;
            color: #472a02;
            font-weight: bold;
            padding-top: 26px;
            transition: transform 300ms ease;
        }
        &:hover {
            span {
                transform: translate3d(0,-3px,0);
            }
            &::after {
                opacity: 1;
            }
        }
    }
}
