html {
    font-family: 'Microsoft JhengHei';
    width: 100%;
    -webkit-font-smoothing: antialiased;
    height: 100%;
}

// font-family: source-han-sans-cjk-tc, sans-serif;

body {
    width: 100%;
    background-color: #fff4d0;
    height: 100%;
}

.container {
    @include wrap;
}

.wrap {
    max-width: 1920px;
    margin: 0 auto;
    overflow: hidden;
    height: 3130px;
    background: #fff4d0 url('../images/wrap-bg.jpg') no-repeat center top;
}

