.section-b {
    height: 970px;
    position: relative;
    padding-top: 20px;
    box-sizing: border-box;
    &__title {
        width: 580px;
        height: 186px;
        background: transparent url('../images/section-b-title@2x.png') no-repeat 0 0;
        background-size: cover;
        font-size: 0;
        margin: 0 auto;
    }
    &__text {
        font-size: 30px;
        text-align: center;
        color: #454545;
        line-height: 1.4;
        margin-bottom: 8px;
    }
    &__wrap {
        display: flex;
        justify-content: center;
        gap: 60px;
    }
    &__content-1 {
        width: 301px;
        height: 571px;
        background: transparent url('../images/section-b-content-1@2x.png') no-repeat 0 0;
        background-size: cover;
    }
    &__content-2 {
        width: 317px;
        height: 562px;
        background: transparent url('../images/section-b-content-2@2x.png') no-repeat 0 0;
        background-size: cover;
        position: relative;
        h3 {
            font-size: 30px;
            color: #fff;
            text-align: center;
            width: 100%;
            position: absolute;
            left: 0;
            top: 8px;
        }
        span {
            position: absolute;
            left: 0;
            width: 100%;
            font-weight: bold;
            &:nth-of-type(1) {
                font-size: 30px;
                color: #454545;
                text-align: center;
                top: 370px;
            }
            &:nth-of-type(2) {
                width: 146px;
                font-size: 30px;
                color: #ffffff;
                text-align: center;
                top: 496px;
            }
            &:nth-of-type(3) {
                width: 146px;
                font-size: 30px;
                color: #ffffff;
                text-align: center;
                top: 496px;
                left: inherit;
                right: 0;
            }
        }
    }
}