.section-a {
    height: 1080px;
    box-sizing: border-box;
    padding-top: 22px;
    position: relative;
    .sectionASlider {
        height: 100%;
        position: relative;
        z-index: 0;
    }
    .cut-swiper-pagination {
        display: flex;
        justify-content: center;
        bottom: inherit;
        top: 22px;
        gap: 10px;
        position: absolute;
        z-index: 30;
    }
    .cut-swiper-pagination-bullet {
        width: 237px;
        border: 0;
        background-color: #ffce7f;
        font-size: 45px;
        color: #332f2f;
        height: 121px;
        border-radius: 12px;
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        cursor: pointer;
        font-family: source-han-sans-cjk-tc, sans-serif;
    }
    .cus-swiper-pagination-bullet-active {
        background-color: #ff9d00;
    }
    .cus-swiper-button-prev,
    .cus-swiper-button-next {
        width: 61px;
        height: 84px;
        position: absolute;
        left: 50%;
        top: 50%;
        z-index: 50;
        cursor: pointer;
    }
    .cus-swiper-button-prev {
        background: transparent url('../images/navigation-bg@2x.png') no-repeat 0 0;
        background-size: 122px auto;
        transform: translate3d(-609%, -17%, 0);
        &:hover {
            background-position: 0 -84px;
        }
    }
    .cus-swiper-button-next {
        background: transparent url('../images/navigation-bg@2x.png') no-repeat -61px 0;
        background-size: 122px auto;
        transform: translate3d(515%, -17%, 0);
        &:hover {
            background-position: -61px -84px;
        }
    }
}

.section-a-1 {
    padding-top: 142px;
    &__title {
        margin: 0 auto 14px;
        width: 497px;
        height: 113px;
        background: transparent url('../images/section-a-1-title@2x.png') no-repeat 0 0;
        background-size: 100% auto;
        font-size: 0;
    }
    &__bg {
        width: 769px;
        height: 974px;
        top: 22px;
        position: absolute;
        left: 50%;
        transform: translate3d(-125%,0,0);
        background: transparent url('../images/section-a-1-bg@2x.png') no-repeat 0 0;
        background-size: 100% auto;
        z-index: 0;
    }
    &__content-1 {
        width: 607px;
        height: 308px;
        background: transparent url('../images/section-a-1-content-1@2x.png') no-repeat 0 0;
        background-size: 100% auto;
        margin: 0 auto 10px;
        position: relative;
        h3 {
            font-size: 30px;
            color: #454545;
            text-align: center;
            position: absolute;
            top: 14px;
            width: 100%;
            font-family: source-han-sans-cjk-tc, sans-serif;
        }
    }
    &__content-2 {
        width: 607px;
        height: 308px;
        background: transparent url('../images/section-a-1-content-2@2x.png') no-repeat 0 0;
        background-size: 100% auto;
        margin: 0 auto 6px;
        position: relative;
        h3 {
            font-size: 30px;
            color: #454545;
            text-align: center;
            position: absolute;
            top: 14px;
            width: 100%;
            font-family: source-han-sans-cjk-tc, sans-serif;
        }
    }
    &__text {
        font-size: 24px;
        text-align: center;
        color: #906621;
        font-weight: bold;
        line-height: 1.1;
        margin-bottom: 8px;
    }
    &__btn {
        width: 276px;
        height: 102.5px;
        background: transparent url('../images/btn-bg-sm@2x.png') no-repeat 0 0;
        background-size: 100% auto;
        display: flex;
        justify-content: center;
        margin: 0 auto;
        position: relative;
        &::after {
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            background: transparent url('../images/btn-bg-sm@2x.png') no-repeat 0 -102.5px;
            background-size: 100% auto;
            transition: opacity 300ms ease;
            opacity: 0;
            z-index: 0;
        }
        span {
            position: relative;
            z-index: 10;
            font-size: 35px;
            color: #472a02;
            font-weight: bold;
            padding-top: 23px;
            transition: transform 300ms ease;
        }
        &:hover {
            span {
                transform: translate3d(0,-3px,0);
            }
            &::after {
                opacity: 1;
            }
        }
    }
}

